.greeting-button{
    background: pink;
    border-color: hotpink;
    width: 300px;
    margin-bottom: 5px;
}

.Home-page{
    height: 100vh;
    display: flex;
    flex-direction: column;
}

@media (max-width: 320px) {
    .Home-page{
        height: 100vh;
        display: flex;
        flex-direction: column;
        align-items: center;
        justify-content: center;
    }

    .greeting-button{
        background: pink;
        box-shadow: hotpink;
        width: 300px;
    }
}