#tests{
    display: flex;
    flex-direction: column;
    border: 3px solid #dda0dd;
    border-radius: 15px;
}

#tests a:visited {
    color: black;
}

#tests a{
    border-bottom:3px solid #dda0dd;
    padding: 5px;
}

#tests a:last-child{
    border: none;
}

.allTest h3{
    display: flex;
    justify-content: center;
    margin: 15px;
}